<template>
  <v-container>
    <v-layout row wrap justify-center>
      <v-flex x12>
          <v-data-table
          :headers="headers"
          :items="formatedServers"
          :pagination.sync="pagination"
          class="elevation-1"
        >
          <template  v-slot:items="props">
            <td @click="pushServer(props.item.server_id)">  
              <v-layout align-center row>
                  <img class="small_avatar" :src="props.item.image_url" :alt="props.item.name">
                  <span class="ml-3 body-2">{{ props.item.name }}</span>
              </v-layout>
            </td>
            <td class="text-xs-right">{{ props.item.players }}</td>
            <td class="text-xs-right" align-center>{{ props.item.totalPoints }}</td>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapState, mapMutations} from 'vuex';

export default {
  name: "home",
  data (){
    return {
        pagination : {
          sortBy: 'totalPoints',
          descending: true
        },
        headers: [
          { text: 'Server', value: 'name' },
          { text: 'Igrača', value: 'players', align: 'right' },
          { text: 'Poeni', value: 'totalPoints', align: 'right' }
        ]
    }
  },
  created(){
    this.getServers()
  },
  methods: {
    ...mapActions(['getServers']),
    pushServer(id){
      console.log(id)
      this.$router.replace({path : `/servers/${id}`})
    }
  },
  computed: {
    ...mapState(['servers']),
    formatedServers(){
      let omg = this.servers.map(server=>{
        server.totalPoints = 0
        server.players = 0
        for(let player in server.points){
          server.totalPoints += server.points[player]
          server.players ++
        }
        return server
      })
      return omg
    }
  }
};
</script>

<style>
.small_avatar {
  max-width: 50px;
}
</style>
