import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import Players from "./views/Players.vue";
import Servers from "./views/Servers.vue";
import Player_profile from "./views/Player_profile.vue";
import Server_profile from "./views/Server_profile.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/players",
      name: "players",
      component: Players,
    },
    {
      path: "/players/:id",
      name: "player_profile",
      component: Player_profile,
    },
    {
      path: "/servers",
      name: "servers",
      component: Servers,
    },
    {
      path: "/servers/:id",
      name: "server_profile",
      component: Server_profile,
    },
  ]
});
