import Vue from "vue";
import Vuex from "vuex";
import axios from 'axios'

axios.defaults.baseURL = process.env.VUE_APP_HOST;
axios.defaults.headers.post['Content-Type'] = 'application/json';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    servers: [],
    players: [],
    player: {}
  },
  mutations: {
    updateServers (state,data) {
      state.servers = data
    },
    updatePlayers (state,data) {
      state.players = data
    },
    updatePlayer (state,data) {
      state.player = data
    },
  },
  actions: {
    getServers({commit}){
      axios.get('/servers').then((res)=>{
        console.log(res.data)
        commit('updateServers', res.data)
      })
    },
    getPlayers({commit}){
      axios.get('/players').then((res)=>{
        console.log(res.data)
        commit('updatePlayers', res.data)
      })
    },
    getPlayer({commit},id){
      axios.get('/players/' + id).then((res)=>{
        console.log(res.data)
        commit('updatePlayer', res.data)
      })
    }
  }
});
