<template>
  <v-container>
    <v-layout row wrap>
      <v-flex x12>
          <v-data-table
          :headers="headers"
          :items="players"
          :pagination.sync="pagination"
          class="elevation-1"
        >
          <template v-slot:items="props">
            <td @click="pushPlayer(props.item.discord_id)">  
              <v-layout align-center row>
                  <img class="small_avatar" :src="props.item.avatar_url" :alt="props.item.nickname">
                  <span class="ml-3 body-2">{{ props.item.nickname }}</span>
              </v-layout>
            </td>
            <td class="text-xs-right">{{ props.item.totalPoints }}</td>
            <td class="text-xs-right">{{ props.item.energy }}/15</td>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapState, mapMutations} from 'vuex';

export default {
  name: "home",
  data (){
    return {
        pagination : {
          sortBy: 'totalPoints',
          descending: true
        },
        headers: [
          { text: 'Igrač', value: 'nickname' },
          { text: 'Poeni', value: 'totalPoints', align: 'right' },
          { text: 'Energija', value: 'energy', align: 'right' }
        ],
    }
  },
  created(){
    this.getPlayers()
  },
  methods: {
    ...mapActions(['getPlayers']),
    pushPlayer(id){
      this.$router.replace({path : `/players/${id}`})
    }
  },
  computed: {
    ...mapState(['players'])
  }
};
</script>
