<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12 >
        <v-layout row align-center mb-4>
            <img :src="player.avatar_url"  :alt="player.nickname">
            <div>
              <span class="ml-4 headline">{{player.nickname}}</span>
              <span class="ml-4 subheading" style="display:-webkit-box">Poena: {{formatedPlayer.totalPoints}}</span>
              <span class="ml-4 subheading" style="display:-webkit-box">Energije: {{formatedPlayer.energy}}/15</span>
            </div>

        </v-layout>
        <hr>
      </v-flex>
      
      <v-flex xs12 mt-4>
        <v-data-table
          :headers="headers"
          :items="formatedPlayer.servers"
          :pagination.sync="pagination"
          class="elevation-1"
        >
          <template v-slot:items="props">
            <td @click="pushServer(props.item.id)">  
              <v-layout align-center row>
                  <img class="small_avatar" :src="props.item.image_url" :alt="props.item.name">
                  <span class="ml-3 body-2">{{ props.item.name }}</span>
              </v-layout>
            </td>
            <td class="text-xs-right">{{ props.item.points }}</td>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapState, mapMutations} from 'vuex';

export default {
  name: "home",
  data (){
    return {
        pagination : {
          sortBy: 'points',
          descending: true
        },
        headers: [
          { text: 'Server', value: 'name' },
          { text: 'Poeni', value: 'points', align: 'right' },
        ],
    }
  },
  created(){
    let {id} = this.$route.params
    this.getPlayer(id)
    this.getServers()
  },
  methods: {
    ...mapActions(['getPlayer','getServers']),
    pushServer(id){
      console.log(id)
      this.$router.replace({path : `/servers/${id}`})
    }
  },
  computed: {
    ...mapState(['player','servers']),
    formatedPlayer(){
      this.player.servers = []
      this.player.totalPoints = 0
      for(let server in this.player.points){
        let serverObj = this.servers.find(srv=>srv.server_id == server)
        this.player.servers.push({name:serverObj.name, image_url:serverObj.image_url, points: this.player.points[server], id:server})
        this.player.totalPoints += this.player.points[server]
      }
      return this.player
    }
  }
};
</script>
