<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12 >
        <v-layout row align-center mb-4>
            <img :src="server.image_url"  :alt="server.name">
            <div>
              <span class="ml-4 headline">{{server.name}}</span>
              <span class="ml-4 subheading" style="display:-webkit-box">Poena:{{server.totalPoints}}</span>
              <span class="ml-4 subheading" style="display:-webkit-box">Igrača: {{server.players ? server.players.length : 0}}</span>
            </div>
        </v-layout>
        <hr>
      </v-flex>
      
      <v-flex xs12 mt-4>
        <v-data-table
          :headers="headers"
          :items="server.players"
          :pagination.sync="pagination"
          class="elevation-1"
        >
          <template v-slot:items="props">
            <td @click="pushPlayer(props.item.id)">  
              <v-layout align-center row>
                  <img class="small_avatar" :src="props.item.image_url" :alt="props.item.name">
                  <span class="ml-3 body-2">{{ props.item.name }}</span>
              </v-layout>
            </td>
            <td class="text-xs-right">{{ props.item.points }}</td>
          </template>
        </v-data-table>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapState, mapMutations} from 'vuex';

export default {
  name: "home",
  data (){
    return {
        pagination : {
          sortBy: 'points',
          descending: true
        },
        headers: [
          { text: 'Igrač', value: 'name' },
          { text: 'Poeni', value: 'points', align: 'right' },
        ],
    }
  },
  created(){
    let {id} = this.$route.params
    this.getPlayers()
    this.getServers()
  },
  methods: {
    ...mapActions(['getPlayers','getServers']),
    pushPlayer(id){
      this.$router.replace({path : `/players/${id}`})
    }
  },
  computed: {
    ...mapState(['players','servers']),
    server(){
      let server = this.servers.find(srv=>srv.server_id == this.$route.params.id)
      if(server) {
        server.players = []
        server.totalPoints = 0
        for(let player_id in server.points){
          let playerObj = this.players.find(ply=>ply.discord_id == player_id)
          server.players.push({name:playerObj.nickname, image_url:playerObj.avatar_url, points: server.points[player_id], id:player_id})
          server.totalPoints += server.points[player_id]
        }
        console.log(server)
        return server
      }else {
        return []
      }

    }
  }
};
</script>
